<template>
  <div class="chat">
    <div class="chat__sidebar">
      <h3>Users</h3>
      <div id="users"></div>
    </div>
    <div class="chat__main list-group">
      <ul id="xd" ref="messages_list" class="chat__messages" >
        <li class="list-group-item" v-for="(msg,index) in messages" :key="msg.createdAt+index">
          <div class="msg-avatar">
            <img src="./assets/user.png" alt="Avatar" />
            <span class="timestamp">{{msg.createdAt}}</span>
          </div>


          <div class="msg-text">
            <span class="from">{{msg.from}}</span>
            <p>{{msg.text}}</p>
          </div>
        </li>
      </ul>
      <div class="chat__footer">
        <form @submit.prevent="sumbit" id="message-form" autocomplete="off">
          <input id="msgInput" type="text" name="message" placeholder="type" autofocus />
          <button>Send</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      messages: []
    }
  },
  methods: {
    sumbit(e) {
      const text = e.target.elements.message.value;
      const msg = { from: 'anonymous', text }
      this.$socket.emit('createMsg', msg, function () {
          e.target.elements.message.value = '';
      });
    },
    scrollToBot(){
      let messages= this.$refs.messages_list;
      let clientHeight = messages.clientHeight;
      let scrollTop = messages.scrollTop;
      let scrollHeight = messages.scrollHeight;
      let newMsg = messages.lastChild;
      let newMsgheight=newMsg?newMsg.clientHeight:0;
      if(clientHeight+scrollTop+newMsgheight*2>=scrollHeight){
        messages.scrollTop=scrollHeight;
      }  
    }
  },
  mounted(){
     this.scrollToBot();
  },
  created(){
    this.$socket.on('newMsg', (newMsg)=>{
      this.messages.push(newMsg);
    });
  },
  updated(){
    this.scrollToBot();
  }
}
</script>

<style>
* {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
   font-family: "Lato", sans-serif;
   font-weight: 400;
}

.from {
   color: rgb(49, 43, 43);
   font-weight: 700;
   font-size: 17px;
   text-transform: capitalize;
}

.timestamp {
   color: rgb(63, 63, 63);
   font-size: 13px;
   font-family: "Roboto", sans-serif;
   margin-top: 3px;
   font-weight: 400;
   display: block;
}

p {
   margin: 0;
   font-family: "Roboto", "Lato", sans-serif;
}

.msg-avatar {
   position: absolute;
   left: -60px;
   top: -20px;
   /* background-color: skyblue; */
   vertical-align: middle;
   text-align: center;
}

.msg-avatar img {
   height: 45px;
   width: 45px;
   /* vertical-align: middle; */
}

.list-group-item {
   margin-top: 20px;
   margin-left: 60px;
   margin-bottom: 35px;
   max-width: 500px;
   width: fit-content;
   position: relative;
   display: block;
   padding: .75rem 1.25rem;
   border-radius: 8px;
   background-color: white;
   border: 1px solid rgba(0, 0, 0, .125);
}

.list-group-item:first-child {
   border-radius: 8px;
}

.list-group-item:last-child {
   border-radius: 8px;
}

h3 {
   font-weight: 600;
   text-align: center;
   font-size: 1.5rem;
}

button {
   border: none;
   background-color: #943CC6;
   color: white;
   cursor: pointer;
   font-weight: 700;
   border-radius: 3px;
   font-size: 17px;
   padding: 0px 18px;
   transition: all .3s ease;
   backface-visibility: hidden;
}

button:hover {
   transform: translateY(-2px);
}

button:active {
   transform: translateY(2px);
}

button:disabled {
   cursor: default;
   background: #698ea5;
}

.centered-form {
   display: flex;
   align-items: center;
   height: 100vh;
   width: 100vw;
   justify-content: center;
   background: linear-gradient(325deg, #943CC6 0%, rgba(49, 84, 129, 1) 100%);
   background: linear-gradient(325deg, rgba(39, 107, 130, 1) 0%, rgba(49, 84, 129, 1) 100%);
   /* w3c */
}

.centered-form__form {
   background: rgba(250, 250, 250, 0.9);
   border: 1px solid #e1e1e1;
   border-radius: 5px;
   padding: 0px 20px;
   margin: 20px;
   width: 230px;
}

.form-field {
   margin: 20px 0;
}

.form-field>* {
   width: 100%;
}

.form-field label {
   display: block;
   margin-bottom: 7px;
}

.form-field input,
.form-field select {
   border: 1px solid #e1e1e1;
   padding: 10px;
   border-radius: 5px;
}

.chat {
   display: flex;
}

.chat__sidebar {
   overflow-y: auto;
   width: 0%;
   height: 100vh;
   background: #242A31;
}

.chat__sidebar h3 {
   color: #e6eaee;
   margin: 10px 20px;
   text-align: left;
}

.chat__sidebar ul {
   list-style-type: none;
}

.chat__sidebar li {
   background: #e6eaee;
   border: 1px solid #e1e1e1;
   border-radius: 5px;
   padding: 10px;
   margin: 10px;
}

.chat__main {
   display: flex;
   flex-direction: column;
   height: 100vh;
   width: 100%;
   overflow-wrap: break-word;
   background-color: #ECEEF1;
}

.chat__messages {
   flex-grow: 1;
   overflow-y: auto;
   overflow-wrap: break-word;
   -webkit-overflow-scrolling: touch;
}

.chat__footer {
   /* background: #f5f5f5; */
   /* background: rgba(145, 12, 207,1); */
   display: flex;
   padding: 10px;
   /* height: 60px; */
   flex-shrink: 0;
}

.chat__footer form {
   flex-grow: 1;
   display: flex;
}

.chat__footer form * {
   margin-right: 10px;
}

.chat__footer input {
   border: none;
   padding: 13px;
   flex-grow: 1;
   border-radius: 5px;
}

.chat__messages {
   list-style-type: none;
   padding: 10px;
}

.message {
   padding: 10px;
}

.message__title {
   display: flex;
   margin-bottom: 5px;
}

.message__title h4 {
   font-weight: 600;
   margin-right: 10px;
}

.message__title span {
   color: #999;
}

@media (max-width: 600px) {
   * {
      font-size: 1rem;
   }
   .from {
      font-size: 15px;
   }
   .timestamp {
      font-size: 12px;
   }
   ul,
   li {
      font-size: 17px;
      padding: 8px 12px;
      word-wrap: break-word;
      display: block;
      border-radius: 7px;
   }
   .list-group-item {
      max-width: 350px;
   }
   .chat__sidebar {
      display: none;
   }
   .chat__footer {
      flex-direction: column;
   }
   .chat__footer form {
      margin-bottom: 10px;
   }
   .chat__footer button {
      margin-right: 0px;
   }
}
</style>



